import React, { useState } from 'react';
import { InputText } from '../../components/atoms/inputs/InputText';
import { Button } from '../../components/atoms/button/button';
import { useStore } from '../../store/context';
import { loginConnect } from '../../helpers/login';
import { Redirect } from 'react-router-dom';

export function LoginPage() {
	const { state, dispatch } = useStore();
	const [logLocal, settLog] = useState('');
	const [passwordLocal, settPassword] = useState('');
	const [handleError, setHandleError] = useState(false);
	const handleLogin = e => settLog(e);
	const handlePassword = e => settPassword(e);

	const handleConnect = async e => {
		e.preventDefault();
		const token = await loginConnect(logLocal, passwordLocal);
		if (token.status === 'OK') {
			handleError && setHandleError(true);
			return dispatch({
				type: 'login',
				payload: token
			});
		}
		return setHandleError(true);
	};

	if (localStorage.localToken) {
		return <Redirect to='/' />;
	}
	return (
		<section className='login_page'>
			<div>{handleError && 'vos identifiants sont incorrects'}</div>
			<form className='form_connect' method='post' onSubmit={handleConnect}>
				<InputText
					isMail
					label='Votre login'
					onChangeValue={handleLogin}
					defaultValue=''
					onEnter={handleConnect}
				/>
				<InputText
					isPassword
					label='Votre mot de passe'
					onChangeValue={handlePassword}
					defaultValue=''
					onEnter={handleConnect}
				/>
				<Button label='Se connecter !' onClick={handleConnect} />
			</form>
		</section>
	);
}
