import React from 'react';
import PropTypes from 'prop-types';
import Badge from '../../../atoms/badge/badge';
import { Button } from '../../../atoms/button/button';
import { Icon } from '../../../atoms/icon/icon';

export default function VignetNextGame({ gameInfos, type, onClick }) {
	const game = gameInfos;

	return (
		<div className={`vignet next_game ${type}`}>
			<div className='row'>
				<div className='col-85'>
					<h3>{game.day}</h3>
					<p className='location'>
						<Icon name='stadium' color='#2C4153' size='30' />{' '}
						<span className='game_location_name'>{game.city}</span>
					</p>
					<div className='vignet_bets_container'>
						<h4>les paris ouverts :</h4>
						<div className='horizontal_list vignet_bets_list'>
							{game.betstypes.map(bet => (
								<Badge
									key={bet.id}
									label={bet.name}
									className='vignet_bet'
								/>
							))}
						</div>
					</div>
				</div>
				<div className='col-15 btn-fullsize'>
					<Button
						label='pariez !'
						className='btn_fullsize'
						onClick={onClick}
					/>
				</div>
			</div>
		</div>
	);
}

VignetNextGame.propTypes = {
	gameInfos: PropTypes.object,
	type: PropTypes.string,
	onClick: PropTypes.func
};
