import React from 'react';
import PropTypes from 'prop-types';
import Badge from '../../../atoms/badge/badge';
import { Icon } from '../../../atoms/icon/icon';

export default function VignetFutureGame({ gameInfos, onClick }) {
	const game = gameInfos;

	return (
		<div className={`vignet future_game`}>
			<div className='row'>
				<div className='col-85'>
					<h4>{game.day}</h4>
					<p className='location'>
						<Icon name='stadium' color='#2C4153' size='30' />{' '}
						<span className='game_location_name'>{game.city}</span>
					</p>
					{game.betstypes.length > 1 ? (
						<div className='vignet_bets_container'>
							<h4>les paris ouverts :</h4>
							<div className='horizontal_list vignet_bets_list'>
								{game.betstypes.map(bet => (
									<Badge
										key={bet.id}
										label={bet.name}
										className='vignet_bet'
									/>
								))}
							</div>
						</div>
					) : (
						<div>Aucun paris disponnible</div>
					)}
				</div>
			</div>
		</div>
	);
}

VignetFutureGame.propTypes = {
	gameInfos: PropTypes.object,
	onClick: PropTypes.func
};
