import React from 'react';
import PropTypes from 'prop-types';
import { Redirect } from 'react-router-dom';

export function Button({ delayActionTime, onClick, className, label, link }) {
	const handleClick = e => {
		if (delayActionTime) {
			return setTimeout(() => {
				onClick(e);
			}, delayActionTime);
		}
		return onClick(e);
	};

	const handleClickLink = () => {
		if (delayActionTime) {
			return setTimeout(() => <Redirect to={link} />, delayActionTime);
		}
		return <Redirect to={link} />;
	};

	return (
		<input
			onClick={link ? handleClickLink : handleClick}
			type='button'
			className={`btn ${className}`}
			value={label}
		/>
	);
}

Button.propTypes = {
	onClick: PropTypes.func,
	link: PropTypes.string,
	label: PropTypes.string.isRequired,
	className: PropTypes.string,
	delayActionTime: PropTypes.number
};
