import React from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import { createBrowserHistory } from 'history';
import { LoginPage } from '../pages/LoginPage/LoginPage';
import { BetsCalendarPage } from '../pages/BetsCalendarPage/BetsCalendarPage';
import { GameDayPage } from '../pages/GameDay/GameDayPage';
import { StoreProvider } from '../store/context';
import '../style/globalStyle.scss';

const history = createBrowserHistory();

export function RouterApp() {
	let redirected = false;
	const accessToStore = component => {
		if (!localStorage.localToken && component.type.name !== 'LoginPage') {
			return (
				<StoreProvider>
					<LoginPage />
				</StoreProvider>
			);
		}
		return <StoreProvider>{component}</StoreProvider>;
	};

	return (
		<Router history={history}>
			<Route
				exact
				path='/'
				render={() => accessToStore(<BetsCalendarPage />)}
			/>
			<Route
				exact
				path='/login'
				render={() => accessToStore(<LoginPage />)}
			/>
			<Route
				exact
				path='/betscalendar'
				render={() => accessToStore(<BetsCalendarPage />)}
			/>
			<Route
				exact
				path='/gamedays/:id'
				render={() => accessToStore(<GameDayPage />)}
			/>
		</Router>
	);
}
