const { localToken } = localStorage;

export const profil = {
	token: localToken || null,
	user: {}
};

export const login = (state, action) => {
	if (action.payload.status === 'OK') {
		const { token, user } = action.payload;
		localStorage.localToken = token;
		const newLogState = {
			...state,
			profil: {
				...state.profil,
				connected: true,
				token: token,
				user: user
			}
		};
		return { ...newLogState };
	}
	return state;
};

export const logout = () => {
	return false;
};
