import React, { useEffect, useState } from 'react';
import { useStore } from '../../store/context';
import { getAllDatas } from '../../helpers/betsdata';
import { Button } from '../../components/atoms/button/button';
import Switch from 'react-switch';
import { Modal } from '../../components/molecules/modal/modal';
import VignetPastGame from '../../components/molecules/vignets/vignetPastGame/vignetPastGame';
import VignetNextGame from '../../components/molecules/vignets/vignetNextGame/vignetNextGame';
import VignetFutureGame from '../../components/molecules/vignets/vignetFutureGame/vignetFutureGame';

export function BetsCalendarPage(props) {
	const { state, dispatch } = useStore();

	const [pageLoaded, setPageLoaded] = useState(false);
	const [isModalNextGameOpen, setModalNextGameOpen] = useState(false);
	const [modalFirstBetValue, setModalFirstBetValue] = useState(false);

	const loadpage = async () => {
		const allDatas = await getAllDatas(state.profil.token);
		dispatch({
			type: 'storegamedays',
			payload: [allDatas[0], allDatas[1]]
		});
		dispatch({
			type: 'saveplayers',
			payload: allDatas[2]
		});
		return { status: 'ok' };
	};

	const nextGameBet = () => {
		return setModalNextGameOpen(true);
	};

	const betsComponentSwitcher = bettype => {
		switch (bettype) {
			case 'Equipe':
				return 'Quelle equipe';
			case 'Joueur':
				return (
					<div>
						<select>
							{state.playersTeam.map((player, id) => (
								<option key={id}>{player}</option>
							))}
						</select>
					</div>
				);
			case 'Nombre':
				return 'Quel nombre';
			case 'Oui/Non':
				return (
					<div>
						{modalFirstBetValue ? 'Oui' : 'Non'}
						<Switch
							onChange={setModalFirstBetValue}
							checked={modalFirstBetValue}
							className='react-switch'
							id='normal-switch'
						/>
					</div>
				);
			case 'Temps':
				return 'Quel temps';
			default:
				return 'fuck';
		}
	};

	function modalNextGameManager() {
		return (
			<Modal closeAction={() => setModalNextGameOpen(false)}>
				{state.gamedays.next.betstypes.map(bet => {
					console.log(bet.betstype_categorie.name);
					return (
						<div key={bet.id}>
							{bet.name}
							{betsComponentSwitcher(bet.betstype_categorie.name)}
						</div>
					);
				})}
				<Button label='Soumettre le pari !' />
			</Modal>
		);
	}

	useEffect(() => {
		if (!pageLoaded && state.gamedays.next === null) {
			loadpage();
		}
		if (state.gamedays.next !== null) {
			setPageLoaded(true);
		}
		console.log(state);
	}, [pageLoaded, state]);

	if (pageLoaded) {
		return (
			<div className='page calendar'>
				<section className='section_games'>
					<div className='next_game_container'>
						<h2>Prochaine journée de match</h2>
						<VignetNextGame
							gameInfos={state.gamedays.next}
							onClick={nextGameBet}
						/>
					</div>
				</section>
				<section className='section_games'>
					<div className='future_games_container'>
						<h2>Matchs à venir</h2>
						<div className='future_games_list'>
							{state.gamedays.futuregames.map(game => (
								<VignetFutureGame key={game.id} gameInfos={game} />
							))}
						</div>
					</div>
				</section>
				<section className='section_games'>
					<div className='past_games_container'>
						<h2>Matchs déjà joués</h2>
						<div className='past_games_list'>
							{state.gamedays.pastgames.map(game => (
								<VignetPastGame key={game.id} gameInfos={game} />
							))}
						</div>
					</div>
				</section>
				{isModalNextGameOpen && modalNextGameManager()}
			</div>
		);
	}
	return <div>LOADING...</div>;
}

BetsCalendarPage.propTypes = {};
