import { login, logout } from './profil/profil';
import { storeGameDays } from './gamedays/gamedays';
import { saveplayers } from './players/players';

export function reducerActions(state, action) {
	switch (action.type) {
		case 'login':
			return login(state, action);
		case 'logout':
			return logout(state, action);
		case 'storegamedays':
			return storeGameDays(state, action);
		case 'saveplayers':
			return saveplayers(state, action);
		default:
			console.log('noope');
	}
}

