import React, { useState } from 'react';
import PropTypes from 'prop-types';

export function InputText(props) {
	const handleChangeEvent = e => {
		if (props.onChangeValue) {
			return props.onChangeValue(e.target.value);
		}
	};
	const handleKeyDown = e => {
		if (props.onEnter && e.key === 'Enter') {
			console.log('try to enter ');
			return props.onEnter(e);
		}
	};
	const haveLabel = props.label !== undefined || props.label !== '';
	const typeSwitcher = props.isPassword
		? 'password'
		: props.isMail
		? 'mail'
		: '';
	return (
		<div className='inputText_container'>
			{haveLabel && <label className='inputText_label'>{props.label}</label>}
			<input
				className='inputText_input'
				onKeyDown={handleKeyDown}
				onChange={handleChangeEvent}
				type={typeSwitcher}
				defaultValue={props.defaultValue}
			/>
		</div>
	);
}

InputText.propTypes = {
	defaultValue: PropTypes.string,
	onChangeValue: PropTypes.func,
	onEnter: PropTypes.func,
	label: PropTypes.string,
	id: PropTypes.string,
	isPassword: PropTypes.bool,
	isMail: PropTypes.bool
};
